(function() {
  'use strict';
  angular
    .module('vmsFrontend')
    .factory('userProfile', userProfile);

  /** @ngInject */
  function userProfile(
    $log,
    $q,
    userProfileEndpoint,
    userAuthentication,
    vmsLocalStorage,
    alertMessage
  ) {
    var service = {
      create: create,
      get: get,
      update: update,
      updateAvatar: updateAvatar,
      drop: drop,
      getAttendingProjects: getAttendingProjects,
      forgotPassword: forgotPassword
    };

    $log.debug('userProfile');

    return service;

    function create(user, role) {
      var deferred = $q.defer();

      userProfileEndpoint
        .create(user)
        .then(function(response) {
          var value = response.data;
          var jsonWebToken = response.headers('Authorization');

          $log.debug(value);

          userAuthentication
            .setAuthentication(value.data, jsonWebToken, role);

          deferred.resolve(response.data);
        })
        .catch(function(response) {
          var value = response.data;
          var errors = value.errors;
          var alerts = alertMessage.convertToValidationDanger(errors);

          deferred.reject(alerts);
        });

      return deferred.promise;
    }

    function get() {
      var deferred = $q.defer();

      userProfileEndpoint
        .get()
        .then(function(response) {
          var value = response.data;
          var year = value.data.birth_year;
          value.data.birth_year = new Date(year, 1);

          deferred.resolve(value);
        })
        .catch(function(response) {
          deferred.reject(response.data);
        });

      return deferred.promise;
    }

    function update(data) {
      var deferred = $q.defer();

      if (data.birth_year instanceof Date) {
        var date = data.birth_year;
        data.birth_year = date.getFullYear();
      }

      userProfileEndpoint
        .update(data)
        .then(function(response) {
          var value = response.data;
          var year = value.data.birth_year;
          value.data.birth_year = new Date(year, 1);

          deferred.resolve(response.data);
        })
        .catch(function(response) {
          deferred.reject(response.data);
        });

      return deferred.promise;
    }

    function updateAvatar(avatar, skipProfile) {
      var deferred = $q.defer();

      userProfileEndpoint
        .updateAvatar(avatar, skipProfile)
        .then(function(response) {
          deferred.resolve(response.data);
        })
        .catch(function(response) {});

      return deferred.promise;
    }

    function drop(credentials) {
      var deferred = $q.defer();

      userProfileEndpoint
        .drop(credentials)
        .then(function(response) {
          userAuthentication
            .logout();

          deferred.resolve(response.data);
        })
        .catch(function(response) {
          deferred.reject(response.data);
        });

      return deferred.promise;
    }

    function getAttendingProjects(id) {
      var deferred = $q.defer();

      userProfileEndpoint
        .getAttendingProjects(id)
        .then(function(response) {
          deferred.resolve(response.data.data);
        })
        .catch(function(response) {
          deferred.reject(response.data);
        });

      return deferred.promise;
    }

    function forgotPassword(data) {

      $log.debug("forgotPassword data");
      $log.debug(data);

      var deferred = $q.defer();

      userProfileEndpoint
        .passwordReset(data)
        .then(function(response) {
          deferred.resolve(response.data);
        })
        .catch(function(response) {
          var value = response.data;
          var errors = value.errors;
          var alerts = alertMessage.convertToValidationDanger(errors);

          deferred.reject(alerts);
        });

      return deferred.promise;
    }
  }

})();

