(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .factory('projectHyperlink', projectHyperlink);

  /** @ngInject */
  function projectHyperlink($log, $q, projectHyperlinkEndpoint) {
    var service = {
      getByProjectId: getByProjectId,
      create: create,
      dropByProjectIdAndHyperlinkId: dropByProjectIdAndHyperlinkId,
      createOrUpdate: createOrUpdate
    };

    return service;

    function getByProjectId(projectId) {
      var deferred = $q.defer();

      projectHyperlinkEndpoint
        .getByProjectId(projectId)
        .then(function(response) {
          deferred.resolve(response.data);
        })
        .catch(function(response) {
          deferred.reject(response);
        });

      return deferred.promise;
    }

    function create(projectId, data) {
      var deferred = $q.defer();

      projectHyperlinkEndpoint
        .create(projectId, data)
        .then(function(response) {
          deferred.resolve(response.data);
        })
        .catch(function(response) {
          deferred.resolve(response);
        });

      return deferred.promise;
    }

    function dropByProjectIdAndHyperlinkId(projectId, hyperlinkId) {
      var deferred = $q.defer();

      projectHyperlinkEndpoint
        .dropByProjectIdAndHyperlinkId(projectId, hyperlinkId)
        .then(function() {
          deferred.resolve();
        })
        .catch(function() {
          deferred.reject();
        });

      return deferred.promise;
    }

    function createOrUpdate(projectId, createdHyperlinks, updatedHyperlinks) {
      var deferred = $q.defer();
      var data = {};

      if (angular.isDefined(createdHyperlinks)
        && createdHyperlinks.length > 0) {
        data['create'] = createdHyperlinks;
      }

      if (angular.isDefined(updatedHyperlinks)
        && updatedHyperlinks.length > 0) {
        data['update'] = updatedHyperlinks;
      }

      projectHyperlinkEndpoint
        .createOrUpdate(projectId, data)
        .then(function(response) {
          deferred.resolve(response.data);
        })
        .catch(function(response) {
          deferred.reject(response);
        });

      return deferred.promise;
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .factory('refreshJwtHttpInterceptor', refreshJwtHttpInterceptor);

  /** @ngInject */
  function refreshJwtHttpInterceptor($injector, $q, $log, vmsLocalStorage) {
    $log.debug("### refreshJwtHttpInterceptor ###");

    var service = {

      responseError: function(response) {
        // IMPORTANT!
        // If the service wants use other dependencies, it should use $injector
        // to get the dependencies. Otherwise, the dependency will not be found
        var userAuthentication = $injector.get('userAuthentication');
        var $http = $injector.get('$http');
        var deferred = $q.defer();
        var isAuthenticated = userAuthentication.isAuthenticated();

        $log.debug("### responseError ###");
        $log.debug(response);
        $log.debug('isAuthenticated = ' + isAuthenticated);

        if (response.status === 401 && isAuthenticated) {
          $log.debug("=== 401 ===");

          var successCallback = function(authorizationToken) {
            $log.debug("refresh successCallback()");
            $log.debug(authorizationToken);

            response.config.headers.Authorization = authorizationToken;

            deferred.resolve();
          };
          var failureCallback = function() {
            $log.error("refresh failureCallback()");
            userAuthentication.logout();

            deferred.reject();
          };

          userAuthentication.refreshToken().then(successCallback).catch(failureCallback);

          return deferred.promise.then(function() {
            return $http(response.config);
          });
        }

        $log.debug("### refreshJwtHttpInterceptor reject");

        return $q.reject(response);
      }
    };

    return service;
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .factory('projectCustomFieldEndpoint', projectCustomFieldEndpoint);

  /** @ngInject */
  function projectCustomFieldEndpoint(
    $log,
    $http,
    config
  ) {
    var apiBaseUrl = config.apiBaseUrl;
    var service = {
      getAllByProjectId: getAllByProjectId,
      getMemberDataByProjectId: getMemberDataByProjectId,
      storeOrUpdateByProjectId: storeOrUpdateByProjectId,
      storeOrUpdateMemberDataByProjectId: storeOrUpdateMemberDataByProjectId
    };

    return service;

    function getAllByProjectId(projectId) {
      return $http({
        method: 'GET',
        url: apiBaseUrl + '/projects/' + projectId + '/custom_fields'
      });
    }

    function getMemberDataByProjectId(projectId) {
      return $http({
        method: 'GET',
        url: apiBaseUrl + '/projects/' + projectId + '/members/custom_field_data'
      });
    }

    function storeOrUpdateByProjectId(data, projectId) {
      return $http({
        method: 'POST',
        url: apiBaseUrl + '/projects/' + projectId + '/custom_fields',
        data: data
      });
    }

    function storeOrUpdateMemberDataByProjectId(data, projectId) {
      return $http({
        method: 'POST',
        url: apiBaseUrl + '/projects/' + projectId + '/members/bulk_custom_field_data',
        data: data
      });
    }
  }
})();

