(function() {
    'use strict';

    angular
        .module('vmsFrontend')
        .directive('formatYear', formatYear);

    /** @ngInject */
    function formatYear($filter, $log) {
        return {
            require: 'ngModel',
            link: function(scope, element, attrs, modelCtrl) {

                modelCtrl.$parsers.push(function(inputValue) {

                    var transformedInput = $filter('date')(inputValue, 'yyyy');

                    $log.debug('=== formatYear directive ===');
                    $log.debug(transformedInput);

                    if (transformedInput != inputValue) {
                        modelCtrl.$setViewValue(transformedInput);
                        modelCtrl.$render();
                    }

                    return transformedInput;
                });
            }
        };
    }
})();
(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .filter('formatDateTime', formatDateTime);

  /** @ngInject */
  function formatDateTime($filter, $log) {
    var filter = function(dateString) {
      var dateValue = new Date(dateString);
      var transformedInput = $filter('date')(dateValue, 'yyyy/MM/dd hh:mm', 'UTC');

      $log.debug('=== formatDateTime directive ===');
      $log.debug(transformedInput);

      return transformedInput;
    };

    return filter;
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .directive('editableProjectHyperlinks', editableProjectHyperlinks);

  /** @ngInject */
  function editableProjectHyperlinks() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/editableProjectHyperlinksDirective/editableProjectHyperlinksDirective.html',
      scope: {
        projectId: '=',
        data: '='
      },
      controller: EditableProjectHyperlinksDirectiveController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;
  }

  /** @ngInject */
  function EditableProjectHyperlinksDirectiveController($log, projectHyperlink) {
    var vm = this;
    vm.data = [];

    if (vm.data.length == 0) {
      vm.data.push({
        name: "",
        link: ""
      });
    }

    $log.debug("EditableProjectHyperlinksDirectiveController");

    $log.debug("vm.projectId = " + vm.projectId);

    if (angular.isDefined(vm.projectId)) {
      $log.debug("vm.projectId is defined.");
      $log.debug("vm.projectId = " + vm.projectId);

      var onSuccess = function(value) {
        vm.data = value.data;
      };

      projectHyperlink
        .getByProjectId(vm.projectId)
        .then(onSuccess);
    }

    vm.remove = function(index) {
      $log.debug("remove a project hyperlink");
      $log.debug(index);

      var onSuccess = function() {
        vm.data.splice(index, 1);

        $log.debug(vm.data);
      };

      var onFailure = function(response) {
        $log.error(response);
      };

      if (angular.isDefined(vm.data[index].id)) {
        projectHyperlink
          .dropByProjectIdAndHyperlinkId(vm.projectId, vm.data[index].id)
          .then(onSuccess)
          .catch(onFailure);
      } else {
        vm.data.splice(index, 1);
      }
    }

    vm.append = function() {
      vm.data.push({
        name: "",
        link: ""
      });
    };
  }
})();

